import express from 'express';
const router = express.Router();

import syncCurrentStatus from './ctrls/syncCurrentStatus'
import fetchAccount from './ctrls/fetchAccount'
import fetchOpenBet from './ctrls/fetchOpenBet'
import fetchHistoryBet from './ctrls/fetchHistoryBet'
import fetchTransaction from './ctrls/fetchTransaction'

import purchaseCredit from './ctrls/purchaseCredit'

router.route('/sync-current-status').get(syncCurrentStatus)
router.route('/fetch-account').get(fetchAccount)
router.route('/fetch-open-bet').get(fetchOpenBet)
router.route('/fetch-history-bet').post(fetchHistoryBet)
router.route('/fetch-transaction').post(fetchTransaction)

router.route('/purchase-credit').post(purchaseCredit)

export default router